.app-header {
    display: flex;
    align-items: center;
    padding: 7px 20px;
    .app-logo {
        cursor: pointer;
        h1 {
            color: lightcoral;
        }
    }
    ul {
        margin-inline-start: 20px;
        display: flex;
        list-style: none;
        padding: 0;
        margin: 0;
    }
    li {
        margin-inline-start: 10px;
    }
    .active-nav {
        font-weight: bold;
        color: red;
    }
}
